import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  // 
  { path: '/home', component: () => import('@/components/comic/router-view/Home.vue') },  // 
  { path: '/search', component: () => import('@/components/comic/router-view/Search.vue') }, // 
  { path: '/bookshelf', component: () => import('@/components/comic/router-view/Bookshelf.vue') }, // 
  { path: '/setting', component: () => import('@/components/comic/router-view/Setting.vue') },  // 
  // 
  { path: '/setting/login', component: () => import('@/components/comic/router-view/Login.vue') },  // 
  { path: '/setting/register', component: () => import('@/components/comic/router-view/Register.vue') }, // 
  { path: '/setting/forget', component: () => import('@/components/comic/router-view/Forget.vue') },  // 

  // 
  { path: '/', redirect: '/home' }
]

const comicRouter = createRouter({
  history: createWebHistory(),
  routes
})

export default comicRouter  // 
